import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

export default class KitchenDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orders: [],
      orderArr: []
    };
    this.checkDateMatch = this.checkDateMatch.bind(this);
  }

  componentDidMount() {
    axios
      .get("http://localhost:3000/orders")
      .then(response => {
        this.setState({
          orders: response.data
        });
        this.checkDateMatch();
      })
      .catch(err => {
        console.log(err);
      });
  }

  componentDidUpdate(prevProps) {
    if (prevProps.match.params.id !== this.props.match.params.id) {
      axios
        .get("http://localhost:3000/orders")
        .then(response => {
          this.setState({
            orders: response.data
          });
          this.checkDateMatch();
        })
        .catch(err => {
          console.log(err);
        });
    }
  }

  checkDateMatch() {
    let { params } = this.props.match;
    let thisDay = new Date(parseInt(params.id));
    let thisDateYear = thisDay.getFullYear();
    let thisDateMonth = thisDay.getMonth() + 1;
    let thisDateDay = thisDay.getDate();
    let orderArr = [];
    for (let i = 0; i < this.state.orders.length; i++) {
      let newDate = new Date(this.state.orders[i].date);
      let dateYear = newDate.getFullYear();
      let dateMonth = newDate.getMonth() + 1;
      let dateDay = newDate.getDate();
      let totalDate = `${dateDay}/${dateMonth}/${dateYear}`;
      if (
        thisDateDay === dateDay &&
        thisDateMonth === dateMonth &&
        thisDateYear === dateYear
      ) {
        console.log("it is an match " + totalDate);
        orderArr.push(this.state.orders[i]);
      } else {
        console.log("no match");
      }
    }
    this.setState({ orderArr: orderArr });
    console.log("orderArr: " + this.state.orderArr);
  }

  getThisDate(id) {
    let found = this.state.orders.find(order => {
      let foundOrderFormat = Date.parse(order.date);
      console.log("foundOrderFormat: " + foundOrderFormat);
      let newDate = new Date(parseInt(id));

      let idFormat = Date.parse(newDate);
      console.log("idFormat: " + idFormat);
      return foundOrderFormat === idFormat;
    });
    return found;
  }

  render() {
    const { params } = this.props.match;
    const foundOrders = this.getThisDate(params.id);
    console.log("foundOrders " + foundOrders);

    return (
      <div className="kitchenDetailDiv">
        <ul>
          {this.state.orderArr.length > 0 &&
            this.state.orderArr.map(orderArrItem => {
              return (
                <i>
                  <div>
                    <h3>{orderArrItem.name}</h3>
                    <ul>
                      {orderArrItem.meal.map(thisMeal => {
                        return <li>{thisMeal}</li>;
                      })}
                    </ul>
                    <p>{orderArrItem.notes}</p>
                  </div>
                </i>
              );
            })}
        </ul>
      </div>
    );
  }
}
